.form {
  position: absolute;
  top: 15%;
  right: 5%;
  width: auto;
  height: auto;
  padding: 20px;
  padding-left: 50px;
  padding-right: 50px;
  background-color: #FFF;
  border-radius: 20px; }

@media only screen and (min-width: 478px) and (max-width: 767px) {
  .form {
    position: relative;
    width: 100%;
    height: auto;
    background-color: #FFF;
    border-radius: 20px;
    padding: 5px; }
  .div {
    width: 100%; } }

