.header {
  position: relative;
  text-align: center;
  width: 100%; }

.img {
  width: 100%;
  height: auto; }

@media only screen and (min-width: 768px) {
  .introduction {
    position: absolute;
    top: 15%;
    left: 15%;
    width: 30%; }
  .largeText {
    font-size: 36px;
    color: white; }
  .mediumText {
    font-size: 24px;
    color: white; }
  .smallText {
    font-size: 18px;
    color: white; } }

@media only screen and (min-width: 478px) and (max-width: 767px) {
  .introduction {
    position: absolute;
    text-align: center;
    top: 30px;
    width: 100%; }
  .largeText {
    font-size: 26px;
    color: white; }
  .mediumText {
    font-size: 22px;
    color: white; }
  .smallText {
    font-size: 15px;
    color: white; } }

@media only screen and (min-width: 371px) and (max-width: 477px) {
  .introduction {
    position: absolute;
    text-align: center;
    top: 30px;
    height: 100%;
    width: 100%; }
  .largeText {
    font-size: 25px;
    color: white; }
  .mediumText {
    font-size: 20px;
    color: white; }
  .smallText {
    font-size: 16px;
    color: white; } }

@media only screen and (max-width: 370px) {
  .introduction {
    position: absolute;
    text-align: center;
    top: 30px;
    height: 100%;
    width: 100%; }
  .largeText {
    font-size: 20px;
    color: white; }
  .mediumText {
    font-size: 16px;
    color: white; }
  .smallText {
    font-size: 12px;
    color: white; }
  .img {
    position: relative;
    width: 100%;
    height: 200px; } }



